const Layout: React.FC = () =>{
    return (
        <Grid>
            <ServerList/>
            <ServerName/>
            <ChannelInfo/>
            <ChannelList/>
            <UserInfo/>
            <ChannelData/>
            <UserList/>
        </Grid>
    );
}

export default Layout;