import React from "react";
import { Container,Profile,Avatar,UserData,Icons,MicIcon,HeadphoneIcon,Settingsicon} from './styles'


const Userinfo: React.FC = () =>{
    return (
        <Container>
            <Profile>
                <Avatar />
                <UserData>
                    <strong>Vinicios Ragazzi</strong>
                    <span>#26401</span>
                </UserData>
            </Profile>

            <Icons>
                <MicIcon />
                <HeadphoneIcon />
                <Settingsicon />
            </Icons>
        </Container>
    );
}

export default Userinfo;